import React, { useState } from 'react'
import { WingBlank, Button, Modal } from 'antd-mobile-v2';
import { Mask, Rate } from 'antd-mobile';
import { Link } from 'react-router-dom'
import VideoPlay from '../../compoent/VideoPlay'
import { useTranslation } from 'react-i18next'

import noCover from '../../compoent/noCover.png'
import '../../css/DetailPage.css'

const alert = Modal.alert;
// 
export default function DetailPage(prop) {
    const [visible, setVisible] = useState(false);
    const [visible2, setVisible2] = useState(false);
    let { t, i18n } = useTranslation()
    console.log(prop);
    const showMask = () => {
        if (prop.location.state.video !== '') setVisible(true)
        else if (prop.location.state.iframeVideo !== '') setVisible2(true)
        else alert('No viedo !!', '', [{ text: 'ok', onPress: () => console.log('cancel video') },])


    }
    // useEffect(() => {
    //     setInfo(prop.location.state)
    // }, []);

    return (

        <div className='detail-outer'>
            <p className='detail-word'>{i18n.language === 'en' ? prop.location.state.enName : prop.location.state.name}</p>
            <img className='detail-pic' src={prop.location.state.cover===''?noCover:prop.location.state.cover} alt='' />
            <Rate style={{ margin: '1vh 0 0 7vw', }} readOnly value={prop.location.state.rating} />
            <div className='detail-des'>
                {i18n.language === 'en' ? prop.location.state.enDescription : prop.location.state.description}
            </div>
            <div>
                <Mask destroyOnClose={true} visible={visible} opacity={0.9} onMaskClick={() => setVisible(false)}>
                {/* <div style={{width:'100%',height:'60vw',marginTop:'25vh',background:'white',position: 'absolute'}}> </div> */}
                    <VideoPlay url={prop.location.state.video} />
                </Mask>
                <Mask destroyOnClose={true} visible={visible2} opacity={0.8} onMaskClick={() => setVisible2(false)}>
                    <div style={{width:'100%',height:'65vw',marginTop:'25vh',position: 'absolute'}}>
                        <iframe
                            title='myiframe'
                            allowFullScreen={true}
                            src={prop.location.state.iframeVideo}
                            style={{ width: '100%', height: '100%' }}
                            frameBorder="0">
                        </iframe>
                    </div>

                </Mask>
                <WingBlank size="lg">
                    <Button type="primary"
                        onClick={showMask}
                        style={{ marginTop: '10vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                        {t('play')}
                    </Button>
                    <Link to={{ pathname: '/app' }}>
                        <Button type="primary"
                            style={{ marginTop: '2vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                            {t('backToHome')}
                        </Button>
                    </Link>
                </WingBlank>
            </div>

        </div>


    );

}