import React, { useState } from 'react'
import { Select } from 'antd';
import { countries } from './phoneCodeCountries'
import { useTranslation } from 'react-i18next'
import '../css/phoneCode.css'

//app
export default function PhoneCode(prop) {
    const [pcCode, setpcCode] = useState('');
    const { Option } = Select;
    let { t } = useTranslation()
    //
    function onChange(value) {
        prop.setareaCode(value)
        console.log('change:', value);
        console.log(value.split(' ')[0]);
    }

    function onSearch(val) {
        console.log('search:', val);
    }

    return (
        <div className='phoneCode-outer'>
            <div className='phoneCode-text'>{t('Area Code') + ""} </div>
            <Select
                className='phoneCode-select'
                showSearch
                placeholder="Select a code"
                optionFilterProp="children"
                onChange={onChange}
                onSearch={onSearch}
                // dropdownStyle={{border: '2px solid rgb(67, 67, 67)'}}
                filterOption={(input, option) =>
                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
            >
                {countries.map((ele, ind) => {
                    return <Option key={ind} value={ele.dialCode + ' ' + ele.name}>{ele.dialCode + ' ' + ele.name}</Option>
                })}
                {/* <Option value="jack">Jack</Option>
                <Option value="lucy">Lucy</Option>
                <Option value="tom">Tom</Option> */}
            </Select>,
        </div>
    );
}
