import React, { useState } from 'react'
// import { Button } from 'antd-mobile';
import { Button } from 'antd';
import { Route, Link } from 'react-router-dom';
import { ApolloClient, createHttpLink, InMemoryCache } from '@apollo/client';
import { setContext } from '@apollo/client/link/context';
import { ApolloProvider } from '@apollo/client/react';
import { useTranslation } from 'react-i18next'

import Member from './pages/Member/Member';
import SignUp from './pages/SignUp/SignUp';
import SignSuccess from './pages/SignUp/SignSuccess';
import FindPsw from './pages/FindPsw/FindPsw';
import Feedback from './pages/Feedback/Feedback';
import BackOver2 from './pages/Feedback/BackOver2';
import BugSub from './pages/BugSub/BugSub';
import BackOver from './pages/BugSub/BackOver';
import PaySuccess from './pages/Member/PaySuccess';
import PayFailed from './pages/Member/PayFailed';
import PswResult from './pages/FindPsw/PswResult';
import AboutUs from './pages/AboutUs';
import Test from './pages/test';
import PayBack from './pages/Member/PayBack';
import MgnLogin from './pages/MngSignUp/MgnLogin';
import MgnList from './pages/MngSignUp/MgnList';

import AppHome from './pages/Shop/AppHome';
import ToolPage from './pages/Shop/ToolPage';
import FitnessPage from './pages/Shop/FitnessPage';
import EduPage from './pages/Shop/EduPage';
import GamePage from './pages/Shop/GamePage';
import SnackPage from './pages/Shop/SnackPage';
import AllappPage from './pages/Shop/AllappPage';
import SearchPage from './pages/Shop/SearchPage';
import DetailPage from './pages/Shop/DetailPage';


export default function App() {
  const [flag, setflag] = useState(true);
  let { t,i18n } = useTranslation()

  //Apollo
  const httpLink = createHttpLink({
    // uri:"https://mpwmarsjg4.execute-api.cn-northwest-1.amazonaws.com.cn/",
    uri: "https://tnbmacqww1.execute-api.us-east-1.amazonaws.com",
  });
  const authLink = setContext((_, { headers }) => {
    return {
      headers: {
        ...headers,
        "X-Api-Key": "172df9f6-2241-4f60-a4bb-b54847d5cdb0",
        authorization: ""
      }
    }
  });
  const client = new ApolloClient({
    link: authLink.concat(httpLink),
    cache: new InMemoryCache()
  });

  function test() {
    setflag(!flag)
  }

  const change = () => {
    i18n.changeLanguage(i18n.language === 'en' ? 'zh' : 'en')
  }

  return (
    <ApolloProvider client={client}>
      <div>
        <button onClick={test}>{!flag ? "" : ""}</button>
        <button onClick={change}>{""}</button>
        {flag ?
          <div>
            <Link to="/member">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/signUp">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/findPsw">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/pswResult">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/feedback">
              <Button className="selector-button"></Button>
            </Link>
            <Link to="/bugSub">
              <Button id='userpage' className="selector-button">Bug</Button>
            </Link>
            <Link to="/success">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/payfailed">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/backOver">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/backOver2">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/aboutUs">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/payBack">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/signSuccess">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/app">
              <Button className="selector-button" ></Button>
            </Link>
            <Link to="/test">
              <Button className="selector-button" ></Button>
            </Link>

          </div>
          : <div></div>}

        <div className='pay'>
          <Route path="/member" component={Member} />
          <Route path="/signUp" component={SignUp} />
          <Route path="/findPsw" component={FindPsw} />
          <Route path="/feedback" component={Feedback} />
          <Route path="/bugSub" component={BugSub} />
          <Route path="/success" component={PaySuccess} />
          <Route path="/payfailed" component={PayFailed} />
          <Route path="/pswResult" component={PswResult} />
          <Route path="/backOver" component={BackOver} />
          <Route path="/backOver2" component={BackOver2} />
          <Route path="/aboutUs" component={AboutUs} />
          <Route path="/payback" component={PayBack} />
          <Route path="/signSuccess" component={SignSuccess} />
          <Route path="/mgnLogin" component={MgnLogin} />
          <Route path="/mgnList" component={MgnList} />
          <Route path="/test" component={Test} />
        </div>
        <div className='shop'>
          <Route path="/app" component={AppHome} />
          <Route path="/tool" component={ToolPage} />
          <Route path="/fitness" component={FitnessPage} />
          <Route path="/education" component={EduPage} />
          <Route path="/game" component={GamePage} />
          <Route path="/snack" component={SnackPage} />
          <Route path="/allapp" component={AllappPage} />
          <Route path="/search" component={SearchPage} />
          <Route path="/detail" component={DetailPage} />
        </div>

      </div>
    </ApolloProvider>
  )
}

