import React from 'react'
import '../../css/SignUp.css'
import { useTranslation } from 'react-i18next'
// 
export default function PswResult() {
    let { t, i18n } = useTranslation()
    return (

        <div>
            {i18n.language === 'zh' ?
                <div style={{ fontSize: '20px', margin: '40vh 5vw' }}>
                    <div style={{ marginTop: '5px',marginLeft: 'calc(42vw - 34px)' }}>{t('newPswSuccess')}</div>
                </div>
                : <div style={{ fontSize: '17px', margin: '40vh 1vw' }}>
                    <div style={{ marginTop: '5px',marginLeft: 'calc(25vw - 10px)' }}>{t('newPswSuccess')}</div>
                </div>}
        </div>

    );

}