import React, { useState, useEffect } from 'react'
import { Modal, Toast } from 'antd-mobile-v2';
import { gql, useLazyQuery, useQuery } from '@apollo/client';
import MgnUser from './MgnUser';
import '../../css/SignUp.css'
import './MgnList.css'

const alert = Modal.alert;
const GET_SALERINFO = gql`
query saler($name:String!) {
    saler(name:$name){
         name
       id
       monthlySales
       yearlySales
       totalSales
       users{
       id
       phone
       monthlyPayFee
       totalPayFee
       deviceLimit
       salerRemark
       ttl{
         vipExpiredAt
       }
     }
       updatedAt
       createdAt
       }
}
`;

// 
export default function MgnList(prop) {
    const [word, setword] = useState('');
    const [user, setuser] = useState('');
    const { loading, error, data } = useQuery(GET_SALERINFO, {
        variables: {
            // name: prop.location.query
            name: 'gmhz1234'
        },
    });


    const submitResult = () => {

        if (word === '') {
            alert('');
        }
        else {
            alert('', '', [

                { text: '', onPress: () => console.log('cancel bugsub') },
                {
                    text: '', onPress: () => {
                        let contact = user === "" ? 'unknown' : user
                        console.log(contact, word);


                    }
                },
            ])
        }

    }

    if (loading) {
        return <div className="pageOnloading">
            ...
        </div >
    }

    if (error) {
        return <div className="pageOnerror">
            
        </div >
    }
    if (data) {
        if (data.saler) {
            return <div>
                <div style={{ fontSize: '30px', margin: '4vh 0 0 5vw' }}>
                    <p style={{ float: "right", fontSize: '15px', margin: '2vh 5vw 0 0' }}>{data.saler.name}</p>
                </div>

                <div style={{ marginTop: "25px" }} >
                    <p className='mgnlist-title' style={{ paddingLeft: "20px", width: '16vw', marginLeft: '6vw' }}>ID</p>
                    <p className='mgnlist-title' style={{ paddingLeft: "30px", width: '22vw' }}></p>
                    <p className='mgnlist-title' style={{ paddingLeft: "20px", width: '16vw' }}></p>
                    <p className='mgnlist-title' style={{ paddingLeft: "10px" }}></p>
                    <p className='mgnlist-title' style={{ paddingLeft: "5px" }}></p>
                </div>
                <div className='mgnlist-outer'>
                    {data.saler.users ? data.saler.users.map((ele, ind) => {
                        return <MgnUser key={ind} testkey={ind} userInfo={ele} />
                    })
                        :
                        <div style={{ fontSize: '20px', margin: '15vh 0 0 34vw' }}></div>}
                </div>
                <div className='a-line' style={{ width: '90vw', height: '2px', background: 'black', margin: '6vh 0 0 5vw' }}></div>

                <p className="getearn" style={{ float: "left", margin: '3vh 0 0 5vw ' }}>{data.saler.monthlySales}</p>
                <p className="getearn" style={{ float: "right", margin: '3vh 5vw 0 0' }}>{data.saler.totalSales}</p>
                <p className="getearn" style={{ float: "left", margin: '1vh 0 0 5vw ' }}>{data.saler.monthlySales * 0.15}</p>
                <p className="getearn" style={{ float: "right", margin: '1vh 5vw 0 0' }}>{data.saler.totalSales * 0.15}</p>

            </div>
        }
    }
    return (
        <div>
        </div >
    );

}